.images::after{
    content: '';
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 100px;
    background: linear-gradient(to top, #1c0522, transparent);
    z-index: 11;
}

.images{
    position: relative;
    width: 100%;
    height: 700px;
}

.moonImg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 700px;
    object-fit: cover;
}

#moon{
    mix-blend-mode: screen;
}

#mountains_front{
    z-index: 9;
}

#text{
    position: absolute;
    top: 150px;
    left: 50%;
    transform: translateX(-50%);
    color: #fff;
    white-space: nowrap;
    font-size: 5em;
    z-index: 8;
}